import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'dva';
import moment from 'moment';
import { Row, Col, Card, Checkbox, Table, Input, Modal, Progress, Badge, Button, Dropdown, Icon, Menu, Tooltip, Popconfirm, message, Divider } from 'antd';
const Search = Input.Search;


function subscription(state) {
  const type = state > 80 ? 'success' : state < 30 ? 'error' : 'info';
  const msg = state > 80 ? '' : state < 30 ? '' : '';
  message[type](`${msg}`);
}

function info(title) {
  Modal.info({
    title: <span>{` ${title} `}</span>,
    content: (
      <div>
        <p></p>
        <p>here is operation area</p>
      </div>
    ),
    onOk() { },
    onCancel() { },
    okText: '',
    cancelText: ''
  });
}

const columns = [{
  title: '',
  dataIndex: 'name',
  key: 'name',
  width: 120,
}, {
  title: '',
  dataIndex: 'already',
  key: 'already',
  render: (text) => (
    <span>
      <Badge status="success" text="" />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      <Badge status="error" text="" />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      <Badge status="default" text="" />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      <Badge status="processing" text="" />&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
      <Badge status="warning" text="" />
    </span>
  )
}, {
  title: '',
  dataIndex: 'none',
  key: 'none',
  width: 150,
}, {
  title: '',
  dataIndex: 'state',
  key: 'state',
  width: 90,
  render: (text) => (
    <span>
      <Tooltip title={text > 80 ? '' : text < 30 ? '' : ''}>
        <Progress type="circle" strokeWidth={15} percent={text} format={() => ''} width={15} status={text > 80 ? 'success' : text < 30 ? 'exception' : 'active'} />
        <span style={{ paddingLeft: 8 }}>{`${text}%`}</span>
      </Tooltip>
    </span>
  )
}, {
  title: '',
  dataIndex: 'datetime',
  key: 'datetime',
  width: 120,
}, {
  title: '',
  key: 'action',
  width: 200,
  render: (text, record) => (
    <span>
      <a onClick={() => info(record.name)}></a>
      <Divider type="vertical" />
      <a onClick={() => subscription(record.state)}></a>
      <Divider type="vertical" />
      <Popconfirm placement="left" title={` ${record.name} `} okText="" cancelText="" onConfirm={() => { message.success('') }} onCancel={() => { message.error('') }}>
        <a></a>
      </Popconfirm>
    </span>
  ),
}];

const data = [];
for (let i = 1; i <= 36; i++) {
  data.push({
    key: i,
    name: 'tradecore' + i,
    already: Math.floor((Math.random() * 5) + 1),
    none: ` ${Math.floor((Math.random() * 10) + 1)} `,
    state: Math.floor((Math.random() * 99) + 1),
    datetime: moment().subtract(i, 'days').startOf('day').format('YYYY-MM-DD'),
  });
}

const title = () => <div>
  <Row>
    <Col xs={24} sm={24} md={12} lg={8} xl={8}>
      <h2></h2>
    </Col>
    <Col xs={24} sm={24} md={12} lg={16} xl={16} style={{ textAlign: 'right' }}>
      <Dropdown overlay={<Menu>
        {columns.map((v, k) => {
          return <Menu.Item key={k}><Checkbox defaultChecked={true}>{v.title}</Checkbox></Menu.Item>
        })}
      </Menu>}>
        <Button>
           <Icon type="down" />
        </Button>
      </Dropdown>
      {' '}
      <Search
        placeholder=""
        style={{ width: 200 }}
        onSearch={value => console.log(value)}
      />
    </Col>
  </Row>
</div>;

class CusTableDemo extends React.Component {
  state = {
    bordered: false,
    pagination: {
      showQuickJumper: true,
      showTotal: (total, range) => `${total}`
    },
    size: 'default',
    title,
    showHeader: true,
  }

  render() {
    const { global } = this.props;
    const { siderRespons } = global;
    const extenProps = siderRespons ? Object.assign(this.state, { scroll: { x: 1300 } }) : this.state;

    return (
      <div>
        <Card hoverable>
          <Table {...extenProps} columns={columns} dataSource={data} />
        </Card>
      </div>
    );
  }
}

CusTableDemo.propTypes = {
  global: PropTypes.object,
}

export default connect(({ global }) => ({ global }))(CusTableDemo);



